.container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  max-width: 1440px;
  padding: 16px 16px 32px;
  width: 100vw;
}
@media screen and (orientation: landscape) {
  .container {
    flex-direction: row;
    gap: 3vw;
  }
}
@media screen and (min-width: 784px) {
  .container {
    flex-direction: row;
    gap: 6.6vw;
    padding: 32px 0;
  }
}
@media screen and (min-width: 1002px) {
  .container {
    gap: 95px;
  }
}
.container__image {
  align-items: center;
  display: flex;
  justify-content: center;
  max-height: 330px;
  max-width: 505px;
  overflow: hidden;
  width: 100%;
}
@media screen and (min-width: 784px), (orientation: landscape) {
  .container__image {
    width: 40vw;
  }
}
.container__image__picture {
  height: auto;
  width: 100%;
}
.container__content {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 24px;
  max-width: 380px;
}
.container__content__paragraph {
  margin-top: 24px;
  text-align: justify;
}
.container__content__button {
  align-self: flex-start;
  margin-top: 32px;
  padding: 12px 36px;
}
@media screen and (min-width: 784px) {
  .container__content__button {
    padding: 16px 40px;
  }
}

.popup {
  background: rgba(18, 18, 18, 0.5019607843);
  display: flex;
  height: 100vh;
  overflow: auto;
  position: fixed;
  width: 100vw;
}
.popup__window {
  background: #ffffff;
  height: fit-content;
  margin: auto;
  max-width: 920px;
  min-height: 300px;
  overflow-y: auto;
  padding: 26px 0 56px;
  position: relative;
  width: fit-content;
}
@media screen and (min-width: 1002px) {
  .popup__window {
    padding: 56px 95px;
  }
}
.popup__window__title {
  margin-left: 16px;
}
@media screen and (min-width: 784px), (orientation: landscape) {
  .popup__window__title {
    margin-left: 56px;
  }
}
@media screen and (min-width: 1002px) {
  .popup__window__title {
    margin-left: 0;
  }
}
.popup__window__content {
  margin: 24px 15px auto;
}
@media screen and (min-width: 784px), (orientation: landscape) {
  .popup__window__content {
    margin: 24px 56px auto;
  }
}
@media screen and (min-width: 1002px) {
  .popup__window__content {
    margin: 24px 0 0;
  }
}
.popup__window__close {
  background: transparent;
  padding: 6px;
  position: absolute;
  right: 15px;
  top: 26px;
}
@media screen and (min-width: 1002px) {
  .popup__window__close {
    right: 46px;
    top: 46px;
  }
}
.popup__window__close:focus {
  border: 1px solid #121212;
}
.popup__window__close:hover {
  background: transparent;
}
.popup__window__close:active {
  box-shadow: 0 0 10px rgba(18, 18, 18, 0.5019607843);
}
.popup__window__close__left, .popup__window__close__right {
  background: #000000;
  display: block;
  height: 2px;
  position: absolute;
  left: 0;
  width: 13.5px;
}
.popup__window__close__left {
  top: 0;
  transform: rotate(45deg);
  transform-origin: 0 100%;
}
.popup__window__close__right {
  bottom: 0;
  transform: rotate(-45deg);
  transform-origin: 0 0;
}
.popup__window__reset {
  background-color: #e4c1ae;
  bottom: 20px;
  color: #ffffff;
  padding: 7px 17px;
  position: absolute;
  right: 17px;
}
@media screen and (min-width: 1002px) {
  .popup__window__reset {
    padding: 10px 20px;
    right: 46px;
  }
}

.table {
  margin: 16px auto;
  width: 100%;
}
.table td,
.table th {
  padding: 5px;
}
.table span {
  display: block;
}
.table__loader {
  animation: spinner 1s ease infinite;
  border: 4px dotted rgba(18, 18, 18, 0.5019607843);
  border-left-color: #121212;
  border-right-color: #121212;
  border-top: 8px solid #121212;
  border-radius: 50%;
  height: 4rem;
  margin: auto;
  width: 4rem;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
html {
  color: #121212;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 42px;
  font-weight: 700;
  line-height: 1.2em;
}

p {
  font-weight: 300;
  line-height: 1.5em;
}

button {
  background: #e4c1ae;
  border: 1px solid transparent;
  color: #ffffff;
  cursor: pointer;
  font-weight: 700;
  outline: none;
  border-radius: 32px;
}
button:focus {
  border: 1px solid rgba(18, 18, 18, 0.5019607843);
}
button:hover {
  background: #d7a488;
}
button:active {
  box-shadow: 0 0 10px rgba(18, 18, 18, 0.5019607843);
}